import Vue from 'vue'

export default {
    namespaced: true,

    state: {
        errors: []
    },

    getters: {
        giveErrors(state) {
            return state.errors;
        }
    },


    actions: {
        showNotifyByCode(context, code) {
            Vue.notify(Object.assign(getText(code), { group: "notify-group" }))
        },
    },

    mutations: {
        updateErrors: function (state, data) {
            state.errors = data;
        },

        cleanErrors: function (state) {
            state.errors = [];
        },

    }
}